"use client";
import React from "react";
import {motion} from "framer-motion";
import LiquidChrome from "@/app/effects/LiquidChrome";

export default function LandingSection(): React.JSX.Element {

    interface NavbarPages {
        name: string;
    }

    const navbarPages: Array<NavbarPages> = [
        {name: "Home"},
        {name: "Works"},
        {name: "About"},
        {name: "Contact"}
    ];
    return (
        <React.Fragment>
            <main className={"w-screen h-screen relative"}>
                <motion.div
                    animate={{
                        scale: 1,
                        filter: "blur(0px)"
                    }}
                    initial={{
                        scale: 2,
                        filter: "blur(5px)"
                    }}
                    transition={{
                        duration: 2,
                        delay: 2.5,
                        ease: [0.83, 0, 0.17, 1]
                    }}
                    className={"h-full w-full relative"}>
                    <LiquidChrome
                        baseColor={[0.1, 0.1, 0.1]}
                        speed={0.1}
                        amplitude={0.6}
                        interactive={true}
                    />


                    <section className={"w-screen h-screen absolute top-0 z-[10] bg-black/50 !pt-[22.5vw] pointer-events-none"}>
                        <motion.div
                            animate={{
                                scaleX: 1
                            }}
                            initial={{
                                scaleX: 0,
                            }}
                            transition={{
                                duration: 1.5,
                                delay: 3.5,
                                ease: [0.83, 0, 0.17, 1]
                            }}
                            className={"flex justify-between border-[1px] border-white/50 items-center !mx-[2vw]"}>
                            {
                                navbarPages.map((page: NavbarPages, index: number): React.JSX.Element => {
                                    return (
                                        <motion.p
                                            whileHover={{
                                                backgroundColor: "white",
                                                color: "black"
                                            }}
                                            style={{
                                                backgroundColor: index === 0 ? "white" : "transparent",
                                                color: index === 0 ? "black" : "white"
                                            }}
                                            key={index}
                                            className={`text-white pointer-events-auto !p-[0.5rem] flex-1 text-center ${index !== 0 ? "border-l-[1px] border-white/50" : ""} blurBackground100`}>
                                            {page.name}
                                        </motion.p>
                                    )
                                })
                            }
                        </motion.div>
                    </section>

                </motion.div>
            </main>
        </React.Fragment>
    )
}